import Vue from 'vue'
import Router from 'vue-router'
import TAP from './views/TAP'
import EAP from './views/EAP.vue'
import Personalidade from './views/Personalidade.vue'
import PlanoDeComunicacao from './views/PlanoDeComunicacao.vue'
import FluxoDeConversa from './views/FluxoDeConversa.vue'
import  Visao from './views/Visao.vue'
import Planejamento from './views/Planejamento.vue'
import Arquitetura from './views/Arquitetura.vue'
import Backlog from './views/Backlog.vue'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/tap',
      name: 'tap',
      component: TAP
    },
    {
      path: '/eap',
      name: 'eap',
      component: EAP
    },
    {
      path: '/personalidade',
      name: 'personalidade',
      component: Personalidade
    },
    {
      path: '/comunicacao',
      name: 'comunicacao',
      component: PlanoDeComunicacao
    },
    {
      path: '/fluxo-de-conversa',
      name: 'fluxo-de-conversa',
      component: FluxoDeConversa
    },
    {
      path: '/visao',
      name: 'visao',
      component: Visao
    },
    {
      path: '/planejamento',
      name: 'planejamento',
      component: Planejamento
    },
    {
      path: '/arquitetura',
      name: 'arquitetura',
      component: Arquitetura
    },
    {
      path: '/backlog',
      name: 'backlog',
      component: Backlog
    },
    {
      path: '/',
      name: 'home',
      component: Home
    },
    
  ]
})
