<h1>Documento de Visão</h1>
<h2>1. Introdução</h2>
  <p>O documento de visão define o escopo de alto nível e o propósito do software a ser desenvolvido. Esse visa estabelecer as expectativas e reduzir os riscos do produto protegendo o cliente e os desenvolvedores do projeto.</p>
<h3>1.1 Propósito</h3>
    <p>Este documento tem como objetivo apresentar uma visão ampla sobre o ChatBot Bino deixando claro suas características, utilidades e funcionalidades.</p>
<h3>1.2 Escopo</h3>
  <p>O ChatBot Bino é um projeto realizado para a disciplina Métodos de Desenvolvimento de Software (MDS) do curso Engenharia de Software da Faculdade UnB Gama (FGA) da Universidade de Brasília (UnB).</p>
    <p>O projeto que será realizado pelo grupo 11 composto por 6 alunos, e com a orientação do professor Joênio Marques possui como objetivo auxiliar os alunos no gerenciamento de faltas ao decorrer do semestre e lembrá-los de compromissos acadêmicos. A partir da interação com o ChatBot, o usuário poderá cadastrar sua grade horária e compromissos acadêmicos. Dessa forma o ChatBot enviará uma pergunta no horário da aula para que o usuário marque se foi ou não a aula naquele determinado dia e enviará notificações conforme um compromisso estiver se aproximando.</p>
<h3>1.3 Definições, Acrônimos e Abreviações</h3>
<ul>
  <li>UnB - Universidade de Brasília</li>
  <li>MDS - Métodos de Desenvolvimento de Software</li>
  <li>Bino - O nome do ChatBot</li>
  <li>ChatBot - Programa de computador capaz de conduzir uma conversação através de via auditiva ou texto</li>
  <li>FGA - Faculdade do Gama</li>
</ul><br/>
