<h2>2. Posicionamento</h2>
<h3>2.1 Oportunidade de Negócio</h3>
  <p>Atualmente muitos estudantes da Universidade de Brasília não conseguem gerenciar de uma maneira efetiva o seu número de faltas, o que pode causar até a reprovação em certas disciplinas.</p>
   <p>Outro problema seria a falta de uma forma eficiente de organizar suas tarefas acadêmicas e acabar sendo prejudicado ou pelo acúmulo de matérias ou até mesmo por faltar atividades avaliativas pela falta de organização.</p> 
   <p>O ChatBot oferecerá o serviço de mostrar ao usuário o seu número de faltas, atividades acadêmicas futuras, notificar sobre o risco de faltas limite, questionar ao usuário se ele teve presença ou não no dia, notificar as atividades acadêmicas a medida que elas se aproximam.</p>
 <h3>2.2 Descrição do Problema</h3>
<p><b>O problema de</b>&nbsp;&nbsp;Inacessibilidade de informações aos universitários e demora para retirada de documentos</p>
<p><b>Afeta</b> &nbsp;&nbsp;Agilidade de atualização</p>
<p><b>Cujo impacto é</b>&nbsp;&nbsp;Falta de informação e atraso</p>
<p><b>Uma boa solução seria</b>&nbsp;&nbsp;Automatização de todo o processo</p>
<h3>2.3 Sentença de Posição do Produto</h3>
<p><b>Para</b>
&nbsp;&nbsp;&nbsp;&nbsp;Universidades</p>
<p><b>Que</b>&nbsp;&nbsp;Desejam organizar atividades acadêmicas e controlar as faltas</p>
<p><b>O Bino</b>&nbsp;&nbsp;É um ChatBot</p>
<p><b>Que</b>&nbsp;&nbsp;Visa facilitar a interação entre Universidade de Brasília e universitários</p>
<p><b>Diferente de</b>&nbsp;&nbsp;Páginas em rede sociais</p>
<p><b>Nosso produto</b>&nbsp;&nbsp;Instrui alunos e fornece informações em menor tempo a cerca da Universidade</p>

<h2>3. Envolvidos e Usuários</h2>
<p>Os envolvidos nesse projeto abrangem o grupo de desenvolvimento (MDS), o suporte dos monitores (EPS) na parte de documentação e do professor para auxílio e acompanhamento no desenvolvimento do trabalho.</p>  
<p>O público-alvo do projeto, que irá interagir com o Bino, são alunos da FGA, sejam eles de graduação ou pós-graduação.</p>
<p>O bot Bino trabalha com funcionalidades voltadas para organização de um estudante da Universidade de Brasília propondo praticidade.</p>
