<h2>4.Descrição da Solução</h2>
<h3>4.1 Perspectiva do Produto</h3>
  <p>O ChatBot Bino tem como objetivo otimizar o tempo do universitário, aumentando a facilidade do mesmo em organizar seus horários. Isso inclui o controle de faltas nas disciplinas nas quais o aluno está matriculado, notificando-o de acordo com o limite permitido por cada matéria. Além disso, o bot conta com um sistema de agenda, o qual o aluno pode marcar datas de provas, trabalhos, reuniões e outros utilizando também o sistema de notificações.</p>

<h3>4.2 Resumo dos recurso</h3>

<br/><table class="table table-striped table-bordered">
    <thead>
    <tr>
    <th>Benefício para o Cliente</th>
    <th>Recursos de suporte</th>
    </tr>
    </thead>
    <tbody>
    <tr>
    <td>Fornece avisos sobre datas de provas, trabalhos, faltas e outros</td>
    <td>O Chatbot Bino utiliza informações fornecidas pelo aluno para gerar os avisos para o usuário, de modo que cada um tenha eventos diferentes e personalizados</td>
    </tr>
    <tr>
    <td>Capacidade de interação entre usuário e o Bino</td>
    <td>A partir de um fluxo de diálogo, Bino é capaz de seguir uma conversa não linear e interagir de forma descontraída e informativa</td>
    </tr>
    <tr>
    <td>Facilidade na organização da vida universitária</td>
    <td>O Bino, quando questionado sobre a agenda, prontamente demonstra de forma prática e interativa as informações sobre determinado evento ou faltas da disciplina solicitada</td>
    </tr>
    </tbody>
    </table><br/>

<h2>5. Recursos do Produto</h2>
<p>O ChatBot Bino é capaz de:</p>
<ul>
<li>Realizar avisos prévios de provas;</li>
<li>Realizar avisos prévios de trabalhos;</li>
<li>Realizar alertas ao aluno sobre suas faltas de acordo com a matéria;</li>
<li>Realizar um fluxo de conversa com o usuário de forma descontraída;</li>
</ul><br/>

