<h2>1. Introdução</h2>

<p>O Termo de Abertura de Projeto busca atender a demanda proposta pela disciplina de Método de Desenvolvimento de Software (MDS). O documento  detalha sobre os possíveis riscos, objetivos e propostas  do ChatBot Bino para com a comunidade da FGA.</p>


<h2>2. Descrição</h2>
<p>O Bino é um bot disponibilizado no telegram, voltado para ajudar na organização  dos estudantes da FGA, visando praticidade para gerir faltas, trabalhos, provas e outras atividades acadêmicas.</p>

<h2>3. Propósito e justificativa</h2>
<p>A comunidade FGA possui uma certa dificuldade em acessar, de forma rápida e prática, algumas informações. O Bino surge para suprir a carência de um mecanismo fácil e informal de acessar algumas informações, surgindo com o propósito de disponibilizar algumas funcionalidades, como por exemplo: disponibilizar o cardápio do RU, explicar sobre o passo a passo para obter documentos acadêmicos e, também, enviar o calendário de matrícula. Além disso, o Bino também é capaz de possibilitar com que o usuário opte por receber notificações sobre algumas dessas informações.</p>

<h2>4. Objetivo</h2>
<p>O principal objetivo do projeto é fornecer aos estudantes um mecanismo que facilite a forma de se controlar as faltas e de se manter organizado. Ferramentas que sem o Bino se mostram necessárias devido a uma imensa quantidade de matérias e demandas que os estudantes possuem, o bot visa economizar o tempo do aluno ao tornar o controle de faltas acessível pelo celular bem como marcar e avisar atividades acadêmicas, tudo isso sem a necessidade de instalação de um aplicativo.</p>

<h2>5. Requisitos de alto nível</h2>
<ul>
<li>Interação entre humano e o Bot Bino de uma forma natural e interativa;</li>
<li>Fluxos de conversação práticos;</li>
<li>Construção e escolha da personalidade do bot Bino;</li>
<li>Análise de imagens para a construção da grade horária do aluno.</li>
</ul><br/>

